.locationsContainer{
  height: 100%;
  background-color: rgb(241, 134, 95);
  display: grid;

}
.locationsRow{
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;


}

.locationsRow div{
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  height: 100%;
  border: 5px solid;
}

.HallwayRow{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
.HallwayRow div{
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  height: 70%;
}

.locationBoxes{
  flex-direction: column;
  gap: 5px;
}
.playerIcon {
  font-size: 5px;
  height: 50px;
  width: 50px;
}
